        margin-right: unset;
        -webkit-align-items: center;
                align-items: center;
    }
    
    .header-title-line-space {
        height: 25px;
    }
}
.recent-work-container {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
    background-color: var(--accent-color);
}

.project-details-container {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
}

.store-button-bottom-space {
